<template>
    <div class="record">
      <div class="content">
        <div class="item" v-for="(li, index) in list" :key="index">
          <div class="top">
            <div class="date">日期: {{li.date}}</div>
            <div class="success"> </div>
          </div>
          <div class="center">
            <div class="center-content">
              <img src="../assets/prize_vip2.png" />
              <p>小睡眠VIP会员 - {{li.time}}天</p>
            </div>
          </div>
          <div class="bottom">
            <div class="date"> </div>
            <div class="success">兑换码：{{li.code_number}}</div>
          </div>
        </div>
        <div class="nothing" v-if="show_nothing">
          暂无兑换记录
        </div>
      </div>
    </div>
</template>

<script>
import { callAppRouter } from '@/js/tool'
export default {
  name: 'Record',
  data () {
    return {
      list: [],
      show_nothing: false
    }
  },
  methods: {
    getData () {
      if (!this.$parent.openid) {
        callAppRouter('Login', {}, (res, ed) => {})
        return
      }
      console.log(this.$parent.openid)
      // eslint-disable-next-line
      $.get(this.$parent.url + '/web/v1/cosleepmp/vip/code/exchange/history', {openid: this.$parent.openid}, (res, err) => {
        console.log(res.data)
        console.log(err)
        for (let i of res.data) {
          const format = {
            date: this.WJH.dateFormat(parseInt(i.code_used_time) * 1000, 'yyyy-MM-dd'),
            code_number: i.code_number,
            time: parseInt(i.code_vip_time) / (24 * 60 * 60)
          }
          this.list.push(format)
        }
        if (res.data.length === 0) {
          this.show_nothing = true
        }
      })
    }
  },
  mounted () {
    this.getData()
  }
}
</script>

